import React from 'react'

import { Wrapper, Nav, NavLeft, NavRight, StyledLink } from './styled'
import urls from '../../constants/urls'

const Header = ({ isAuthenticated }) => (
  <Wrapper>
    <Nav>
      <NavLeft>
        <Link to={urls.index}>{'Product List'}</Link>
      </NavLeft>
      <NavRight>
        {isAuthenticated ? (
          <>
            <StyledLink href={urls.myAccount}>My Account</StyledLink>
            <StyledLink href={urls.logout}>Logout</StyledLink>
          </>
        ) : (
          <>
            <StyledLink href={urls.signIn}>Sign In</StyledLink>
            <StyledLink href={urls.signUp}>Sign Up</StyledLink>
          </>
        )}
        <StyledLink to={urls.cart}>{'My Cart'}</StyledLink>
      </NavRight>
    </Nav>
  </Wrapper>
)

export default Header
